import { Pipe, PipeTransform } from '@angular/core';
import { SharedService } from 'src/app/shared/services/shared.service';
import { DomSanitizer } from '@angular/platform-browser';

import * as _ from 'underscore';

import * as _moment from 'moment-timezone';
// tslint:disable-next-line:no-duplicate-imports
import {default as _rollupMoment} from 'moment-timezone';

const moment = _rollupMoment || _moment;


@Pipe({
  name: 'shared'
})
export class SharedPipe implements PipeTransform {

  transform(value: unknown, ...args: unknown[]): unknown {
    return null;
  }

}


@Pipe({
  name: 'userDateFormat'
})
export class UserDateFormatPipe implements PipeTransform {

  transform(date: any | Date, timeZone: any = ''): any {

    if(date != undefined)
      return moment.utc(date).tz(timeZone.region).format(timeZone.date)
    else
      return moment(date).tz(timeZone.region).format(timeZone.date)
  }

}

@Pipe({
  name: 'userDateTimeFormat'
})
export class UserDateTimeFormatPipe implements PipeTransform {

  transform(date: string | Date, timeZone: any = ''): string {
    if(date != undefined)
      return moment.utc(date).tz(timeZone.region).format(timeZone.time)
    else
      return ""
  }

}

@Pipe({
  name: 'userTimeFormat'
})
export class UserTimeFormatPipe implements PipeTransform {

  transform(date: string | Date, timeZone: any = '', hour12Timer:boolean): string {
    if(date != undefined)
      if(hour12Timer)
        return moment.utc(date).tz(timeZone.region).format(timeZone.hours12)
      else
      return moment.utc(date).tz(timeZone.region).format(timeZone.hours)
    else
      return ""
  }

}

@Pipe({
  name: 'safeUrl'
})
export class SafeUrlPipe implements PipeTransform {

  constructor(private sanitizer:DomSanitizer){}

  transform(url) {
    return this.sanitizer.bypassSecurityTrustUrl(url);
  }

}
