import { Component, OnInit, Input, Output, EventEmitter, OnDestroy, ViewEncapsulation, ChangeDetectorRef, ChangeDetectionStrategy, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { MatButton } from '@angular/material/button';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { LearnMediaWatcherService } from 'src/@learn/services/media-watcher/media-watcher.service';


@Component({
  selector: 'table-search',
  templateUrl: './table-search.component.html',
  styleUrls: ['./table-search.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class TableSearchComponent implements OnInit {

  @Input() input:any;

  @Output() outputParams = new EventEmitter();

  isInputSubmitted:boolean = false;

  isMobile:boolean = false;

  private _overlayRef: OverlayRef;
  private _unsubscribeAll: Subject<any>;

  @ViewChild('tableSearchOrigin')
    private _tableSearchOrigin: MatButton;

    @ViewChild('tableSearchPanel')
    private _tableSearchPanel: TemplateRef<any>;

  constructor(
    private _overlay: Overlay,
    private _learnMediaWatcherService: LearnMediaWatcherService,
    private _viewContainerRef: ViewContainerRef
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

  }

  searchData(){

    this.isInputSubmitted = true;
    this.outputParams.emit(this.input)

    if(this.isMobile){

       // Create a portal from the template
       const templatePortal = new TemplatePortal(this._tableSearchPanel, this._viewContainerRef);

       // If overlay exists and attached...
       if ( this._overlayRef && this._overlayRef.hasAttached() )
       {
           // Detach it
           this._overlayRef.detach();
       }

       // If template portal exists and attached...
       if ( templatePortal && templatePortal.isAttached )
       {
           // Detach it
           templatePortal.detach();
       }

    }


  }

  clearData(){
    this.input = "";
    if(this.isInputSubmitted){
      this.outputParams.emit(this.input);
      this.isInputSubmitted = false;
    }
  }

  isDataEmpty(){
    if(this.input == ''){
      if(this.isInputSubmitted){
        this.outputParams.emit(this.input);
        this.isInputSubmitted = false;
      }
    }
  }

  openPanel(): void
   {
       // Create the overlay
       this._overlayRef = this._overlay.create({
           hasBackdrop     : true,
           panelClass: 'search-pane',
           scrollStrategy  : this._overlay.scrollStrategies.block(),
           positionStrategy: this._overlay.position()
                                 .flexibleConnectedTo(this._tableSearchOrigin._elementRef.nativeElement)
                                 .withFlexibleDimensions()
                                 .withViewportMargin(16)
                                 .withLockedPosition()
                                 .withPositions([
                                     {
                                         originX : 'start',
                                         originY : 'bottom',
                                         overlayX: 'start',
                                         overlayY: 'top'
                                     },
                                     {
                                         originX : 'start',
                                         originY : 'top',
                                         overlayX: 'start',
                                         overlayY: 'bottom'
                                     },
                                     {
                                         originX : 'end',
                                         originY : 'bottom',
                                         overlayX: 'end',
                                         overlayY: 'top'
                                     },
                                     {
                                         originX : 'end',
                                         originY : 'top',
                                         overlayX: 'end',
                                         overlayY: 'bottom'
                                     }
                                 ])
       });

       // Create a portal from the template
       const templatePortal = new TemplatePortal(this._tableSearchPanel, this._viewContainerRef);

       // Attach the portal to the overlay
       this._overlayRef.attach(templatePortal);

       // Subscribe to the backdrop click
       this._overlayRef.backdropClick().subscribe(() => {

           // If overlay exists and attached...
           if ( this._overlayRef && this._overlayRef.hasAttached() )
           {
               // Detach it
               this._overlayRef.detach();
           }

           // If template portal exists and attached...
           if ( templatePortal && templatePortal.isAttached )
           {
               // Detach it
               templatePortal.detach();
           }
       });
   }

  ngOnInit(): void {

    // Subscribe to media changes
    this._learnMediaWatcherService.onMediaChange$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe(({matchingAliases}) => {

        if ( matchingAliases.includes('lt-lg') ){
          this.isMobile = true;
        }
        else {

          this.isMobile = false;

          // Create a portal from the template
          const templatePortal = new TemplatePortal(this._tableSearchPanel, this._viewContainerRef);

          // If overlay exists and attached...
          if ( this._overlayRef && this._overlayRef.hasAttached() )
          {
              // Detach it
              this._overlayRef.detach();
          }

          // If template portal exists and attached...
          if ( templatePortal && templatePortal.isAttached )
          {
              // Detach it
              templatePortal.detach();
          }

        }
    });


  }

}
