import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

import { timeZones } from 'src/app/data/mock/common/timezone/data';


@Injectable({
  providedIn: 'root'
})
export class SharedService {

  private userPic = new BehaviorSubject<any>("");
  userpiccast = this.userPic.asObservable();

  private isApartmenSelected = new BehaviorSubject<boolean>(false);
  apartmentselectedcast = this.isApartmenSelected.asObservable();

  private apartmentPic = new BehaviorSubject<any>("");
  apartmentpiccast = this.apartmentPic.asObservable();

  private unitListDeleteIndex = new BehaviorSubject<any>(null);
  unitlistdeleteindexcast = this.unitListDeleteIndex.asObservable();

  private matDrawerId = new BehaviorSubject<any>(null);
  matdraweridcast = this.matDrawerId.asObservable();

  private timeZone = new BehaviorSubject<any>(null);
  timezonecast = this.timeZone.asObservable();

  constructor() { }

  setUnitListDeleteIndex(value) {
    this.unitListDeleteIndex.next(value);
  }

  setApartmentSelected(value){
    this.isApartmenSelected.next(value);
  }

  setUserPic(value){
    this.userPic.next(value)
  }

  setApartmentPic(value){
    this.apartmentPic.next(value)
  }

  getMatDrawerId(){
    return this.matDrawerId.value
  }

  setMatDrawerId(id){
    this.matDrawerId.next(id);
  }

  guid(){
    let d = new Date().getTime();

      // Use high-precision timer if available
      if ( typeof performance !== 'undefined' && typeof performance.now === 'function' )
      {
          d += performance.now();
      }

      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
          const r = (d + Math.random() * 16) % 16 | 0;
          d = Math.floor(d / 16);
          return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
      });
  }

  setTimeZone(value){

    let data = timeZones.filter(item => {
      return item.id == value
    })

    if(data == null){
      this.timeZone.next(null)
    }
    else {
      if(data.length > 0) {
        let date = data[0].date;
        let time = data[0].date +' hh:mm A';
        let time24 = data[0].date +' HH:mm';
        let hours12 = 'hh:mm A';
        let hours = 'HH:mm';

        this.timeZone.next({region: data[0].region, language: data[0].language, offset: data[0].offset, date: date, time: time, time24: time24, hours: hours,  hours12: hours12 })


      }
    }

  }

  getTimeZone(){
    return this.timeZone.value
  }

}
