import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { InitialDataResolver } from 'src/app/app.resolvers';

import { LayoutComponent } from 'src/app/layout/layout.component';


export const appRoutes: Routes = [

  { path: '', pathMatch : 'full', redirectTo: 'login' },

  // Redirect signed in user to the '/dashboards/finance'
  { path: 'signed-in-redirect', pathMatch : 'full', redirectTo: 'program/setup' },

  {
    path: 'login',
    component: LayoutComponent,
    data: {
      layout: 'empty'
    },
    loadChildren: () => import('src/app/modules/authentication/login/login.module').then(m => m.LoginModule)
  },

  // Admin routes
  {
    path       : '',
    component  : LayoutComponent,
    canActivate: [ AuthGuard ],
    resolve    : {
      initialData: InitialDataResolver,
    },
    children   : [
      { path: 'dashboard', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/dashboard/dashboard.module').then(m => m.DashboardModule) },
      {
        path: 'user',
        children: [
          { path: '', pathMatch : 'full', redirectTo: 'setup'},
          { path: 'add', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/user/add-user/add-user.module').then(m => m.AddUserModule) },
          { path: 'unapproved', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/user/unapproved-user/unapproved-user.module').then(m => m.UnapprovedUserModule) },
          { path: 'deactivated', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/user/deactivated-user/deactivated-user.module').then(m => m.DeactivatedUserModule) }
        ]
      },
      {
        path: 'program',
        children: [
          { path: '', pathMatch : 'full', redirectTo: 'setup'},
          { path: 'setup', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/program/program-setup/program-setup.module').then(m => m.ProgramSetupModule) },
          { path: 'add', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/program/add-program/add-program.module').then(m => m.AddProgramModule) },
          { path: 'edit/:id', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/program/add-program/add-program.module').then(m => m.AddProgramModule) },
          { path: 'list', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/program/program-list/program-list.module').then(m => m.ProgramListModule) }
        ]
      },
      {
        path: 'open-programs',
        children: [
          { path: '', pathMatch : 'full', redirectTo: 'setup'},
          { path: 'setup', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/open-program/open-program-setup/open-program-setup.module').then(m => m.OpenProgramSetupModule) },
          { path: 'list', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/open-program/open-program-booking-list/open-program-booking-list.module').then(m => m.OpenProgramBookingListModule) }
        ]
      },
      {
        path: 'link',
        children: [
          { path: '', pathMatch : 'full', redirectTo: 'link-profile'},
          { path: 'link-profile', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/link/link-profile/link-profile.module').then(m => m.LinkProfileModule) },
          { path: 'link-user', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/link/link-user/link-user.module').then(m => m.LinkUserModule) },
          { path: 'reports', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/link/link-reports/link-reports.module').then(m => m.LinkReportsModule) }
        ]
      },
      {
        path: 'notification',
        children: [
          { path: '', pathMatch : 'full', redirectTo: 'setup'},
          { path: 'setup', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/notification/notification-setup/notification-setup.module').then(m => m.NotificationSetupModule) },
          { path: 'add', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/notification/add-notification/add-notification.module').then(m => m.AddNotificationModule) },
          { path: 'list', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/notification/notification-list/notification-list.module').then(m => m.NotificationListModule) },
          { path: 'my-notification', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/notification/my-notification/my-notification.module').then(m => m.MyNotificationModule) }
        ]
      },
      {
        path: 'enquiries',
        children: [
          { path: '', pathMatch : 'full', redirectTo: 'setup'},
          { path: 'setup', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/enquiries/enquiry-setup/enquiry-setup.module').then(m => m.EnquirySetupModule) },
          { path: 'add', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/enquiries/add-enquiry/add-enquiry.module').then(m => m.AddEnquiryModule) },
          { path: 'list', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/enquiries/enquiry-list/enquiry-list.module').then(m => m.EnquiryListModule) }
        ]
      },
      {
        path: 'feedback',
        children: [
          { path: '', pathMatch : 'full', redirectTo: 'setup'},
          { path: 'setup', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/feedback/feedback-setup/feedback-setup.module').then(m => m.FeedbackSetupModule) },
          { path: 'add', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/feedback/add-feedback/add-feedback.module').then(m => m.AddFeedbackModule) },
          { path: 'list', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/feedback/feedback-list/feedback-list.module').then(m => m.FeedbackListModule) }
        ]
      }
    ]

  },

  { path: 'sign-out', loadChildren: () => import('src/app/modules/ui/pages/sign-out/sign-out.module').then(m => m.AuthSignOutModule) },
  {path: 'forgot-password', loadChildren: () => import('src/app/modules/authentication/forgot-password/forgot-password.module').then(m => m.ForgotPasswordModule)},
  {path: 'reset-password', loadChildren: () => import('src/app/modules/authentication/reset-password/reset-password.module').then(m => m.ResetPasswordModule)}
];

@NgModule({
  imports: [RouterModule.forChild(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
