import { Component, HostBinding, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Router } from '@angular/router';
import { Subject } from 'rxjs';

import { LearnNavigationService } from 'src/app/layout/regulars/navigation/navigation.service';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'dense-layout',
  templateUrl: './dense.component.html',
  styleUrls: ['./dense.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DenseLayoutComponent implements OnInit {

  data: any;
  apartmentPicUrl:any;
  isScreenSmall: boolean;
  navigationAppearance: 'classic' | 'dense';

  @HostBinding('class.fixed-header')
    fixedHeader: boolean;

    @HostBinding('class.fixed-footer')
    fixedFooter: boolean;

    // Private

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private sharedService:SharedService,
    private _learnNavigationService: LearnNavigationService
  ) { }

  getAppearanceParams(event){
    this.navigationAppearance = event
  }

  ngOnInit(): void {

    // Subscribe to the resolved route data
    this._activatedRoute.data.subscribe((data: Data) => {
      this.data = data.initialData;
    });


    this.sharedService.apartmentpiccast.subscribe( apartmentPicUrl => {
      this.apartmentPicUrl = apartmentPicUrl
    })

    window.onresize = () => {
      window.innerWidth <= 991 ? this.isScreenSmall = true : this.isScreenSmall = false
    }

    window.innerWidth <= 991 ? this.isScreenSmall = true : this.isScreenSmall = false

  }

}
