import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { LearnMockApi } from 'src/@learn/lib/mock-api/mock-api.interfaces';
import { LearnMockApiService } from 'src/@learn/lib/mock-api/mock-api.service';
import { user as userData } from 'src/app/data/mock/common/user/data';

@Injectable({
    providedIn: 'root'
})
export class UserMockApi implements LearnMockApi
{
    // Private
    private _user: any;

    /**
     * Constructor
     *
     * @param _learnMockApiService
     */
    constructor(
        private _learnMockApiService: LearnMockApiService
    )
    {
        // Set the data
        this._user = userData;

        // Register the API endpoints
        this.register();
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Register
     */
    register(): void
    {
        // -----------------------------------------------------------------------------------------------------
        // @ User - GET
        // -----------------------------------------------------------------------------------------------------
        this._learnMockApiService
            .onGet('api/common/user')
            .reply(() => {
                return [
                    200,
                    {
                        user: _.cloneDeep(this._user)
                    }
                ];
            });

        // -----------------------------------------------------------------------------------------------------
        // @ User - PATCH
        // -----------------------------------------------------------------------------------------------------
        this._learnMockApiService
            .onPatch('api/common/user')
            .reply((request) => {

                // Get the user data
                const user = _.cloneDeep(request.body.user);

                // Update the user data
                this._user = _.assign({}, this._user, user);

                return [
                    200,
                    {
                        user: _.cloneDeep(this._user)
                    }
                ];
            });
    }
}
