import { Component, OnInit, Injector, ViewEncapsulation, EventEmitter, Input, Output } from '@angular/core';
import { combineLatest, Subject } from 'rxjs';
import { filter, map, takeUntil } from 'rxjs/operators';
import { AppConfig } from 'src/app/core/config/app.config';

import { LearnVerticalNavigationAppearance } from 'src/app/layout/regulars/navigation/navigation.types';

import { LearnNavigationService } from 'src/app/layout/regulars/navigation/navigation.service';
import { LearnConfigService } from 'src/@learn/services/config/config.service';

import { AuthService } from 'src/app/core/auth/auth.service';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as _ from 'underscore';

@Component({
  selector: 'header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class HeaderComponent implements OnInit {

  userLoggedIn:boolean = false;
  isApartmentSelected:boolean = false;

  modalService:any;

  navigationAppearance: any;

  config: AppConfig;

  @Input() user:any;

  @Output() toggleNotification = new EventEmitter();

  @Output()
    readonly appearanceChanged: EventEmitter<LearnVerticalNavigationAppearance>;

  // Private
  private _unsubscribeAll: Subject<any>;

  constructor(
    private injector: Injector,
    private _learnNavigationService: LearnNavigationService,
    private _learnConfigService: LearnConfigService,
    private authService: AuthService,
    private sessionService:SessionService,
    private sharedService:SharedService
  ) {
    this.appearanceChanged = new EventEmitter<LearnVerticalNavigationAppearance>();
    this.navigationAppearance = 'dense';
     // Set the private defaults
     this._unsubscribeAll = new Subject();
  }


  toggleNavigation(key): void {
    // Get the navigation
    const navigation = this._learnNavigationService.getComponent(key);
    if ( navigation )
    {
        // Toggle the opened status
        navigation.toggle();
    }
  }

  toggleNavigationAppearance(): void{
        this.navigationAppearance === 'classic' ? this.navigationAppearance = 'dense' : this.navigationAppearance = 'classic';
        this.appearanceChanged.emit(this.navigationAppearance);
  }

  ngOnInit() {

    this.sharedService.apartmentselectedcast.subscribe(isApartmentSelected => this.isApartmentSelected = isApartmentSelected)

    // Subscribe to config changes
    this._learnConfigService.config$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((config: AppConfig) => {

        // Store the config
        this.config = config;

        this.navigationAppearance = this.config.layout;

    });

  }

}
