import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Router, NavigationStart, NavigationEnd } from '@angular/router';
import { delay, filter, takeUntil, distinctUntilChanged } from 'rxjs/operators';
import { Subject, Subscription } from 'rxjs';

import * as _ from 'underscore';

@Component({
  selector: 'page-title',
  templateUrl: './page-title.component.html',
  styleUrls: ['./page-title.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PageTitleComponent implements OnInit {

  data: any;

  title:any;
  breadcrumbItem:any;

  navs:any;
  parentMenu:any = [];

  private _unsubscribeAll: Subject<any>;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
  ) {

    // Subscribe to the resolved route data
    this._activatedRoute.data.subscribe((data: Data) => {

      this.data = data.initialData;
      this.navs = this.data.navigation.default;

    });

   }

  isActiveMenu(item){
    return item.link == this._router.url
  }

  isNoParentNav(){
    return this.parentMenu.length != 0 && this.parentMenu[0].children != undefined ? true : false
  }

  getTitle(breadcrumb) {
    var data = breadcrumb.filter( item => {
      return item.link == this._router.url
    })

    if(data.length == 0 || data == undefined)
    return ''
    else
    return data[0].title
  }

  ngOnInit(): void {

    // Attach a listener to the NavigationEnd event
    this._router.events.
    subscribe((event: NavigationEnd) => {


      let url = this._router.url

      let topNavItem = this.navs.filter( item => {
        return JSON.stringify(item).indexOf(url) !== -1;
      })

      if(topNavItem[0].children != undefined) {

        this.parentMenu = topNavItem[0].children.filter( item => {
          return  _.find(item.children, { link: url });
        })
        this.breadcrumbItem = this.parentMenu[0].children
      }
      else {
        this.parentMenu = topNavItem;
        this.breadcrumbItem = this.parentMenu;
      }

    });

  }

}
