import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { LearnNavigationItem } from 'src/app/layout/regulars/navigation/navigation.types';
import { LearnMockApi } from 'src/@learn/lib/mock-api/mock-api.interfaces';
import { LearnMockApiService } from 'src/@learn/lib/mock-api/mock-api.service';
import { compactNavigation, defaultNavigation } from 'src/app/data/mock/common/navigation/data';

@Injectable({
    providedIn: 'root'
})
export class NavigationMockApi implements LearnMockApi
{
    // Private Readonly
    private readonly _compactNavigation: LearnNavigationItem[];
    private readonly _defaultNavigation: LearnNavigationItem[];
    private readonly _futuristicNavigation: LearnNavigationItem[];
    private readonly _horizontalNavigation: LearnNavigationItem[];

    /**
     * Constructor
     *
     * @param _learnMockApiService
     */
    constructor(
        private _learnMockApiService: LearnMockApiService
    )
    {
        // Set the data
        this._compactNavigation = compactNavigation;
        this._defaultNavigation = defaultNavigation;

        // Register the API endpoints
        this.register();
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Register
     */
    register(): void
    {
        // -----------------------------------------------------------------------------------------------------
        // @ Navigation - GET
        // -----------------------------------------------------------------------------------------------------
        this._learnMockApiService
            .onGet('api/common/navigation')
            .reply(() => {

                // Fill compact navigation children using the default navigation
                this._compactNavigation.forEach((compactNavItem) => {
                    this._defaultNavigation.forEach((defaultNavItem) => {
                        if ( defaultNavItem.id === compactNavItem.id )
                        {
                            compactNavItem.children = _.cloneDeep(defaultNavItem.children);
                        }
                    });
                });

                return [
                    200,
                    {
                        compact   : _.cloneDeep(this._compactNavigation),
                        default   : _.cloneDeep(this._defaultNavigation),
                        futuristic: _.cloneDeep(this._futuristicNavigation),
                        horizontal: _.cloneDeep(this._horizontalNavigation)
                    }
                ];
            });
    }
}
