import {HttpClient, HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';

export interface GetProgramListParams {
  /** format: int32 */
  UserId?: number;
  /** format: int32 */
  ClientId?: number;
}

@Injectable()
export class ProgramService {
  constructor(private http: HttpClient) {}

  /** http://undefined/swagger/swagger-ui.html#!/Program/undefined */
  addUpdateProgram(): Observable<void> {
    return this.http.post<void>(`/api/Program/AddUpdateProgram`, {});
  }

  /** http://undefined/swagger/swagger-ui.html#!/Program/undefined */
  getProgramList(params: GetProgramListParams): Observable<void> {
    const queryParamBase = {
      UserId: params.UserId,
      ClientId: params.ClientId,
    };

    let queryParams = new HttpParams();
    Object.entries(queryParamBase).forEach(([key, value]: [string, any]) => {
      if (value !== undefined) {
        if (typeof value === 'string') queryParams = queryParams.set(key, value);
        else if (Array.isArray(value)) value.forEach(v => queryParams = queryParams.append(key, v));
        else queryParams = queryParams.set(key, JSON.stringify(value));
      }
    });

    return this.http.post<void>(`/api/Program/GetProgramList`, {}, {params: queryParams});
  }
}
