import { Injectable } from '@angular/core';
import { LearnDrawerComponent } from 'src/@learn/components/drawer/drawer.component';

@Injectable({
    providedIn: 'root'
})
export class LearnDrawerService
{
    // Private
    private _componentRegistry: Map<string, LearnDrawerComponent>;

    /**
     * Constructor
     */
    constructor()
    {
        // Set the defaults
        this._componentRegistry = new Map<string, LearnDrawerComponent>();
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Register drawer component
     *
     * @param name
     * @param component
     */
    registerComponent(name: string, component: LearnDrawerComponent): void
    {
        this._componentRegistry.set(name, component);
    }

    /**
     * Deregister drawer component
     *
     * @param name
     */
    deregisterComponent(name: string): void
    {
        this._componentRegistry.delete(name);
    }

    /**
     * Get drawer component from the registry
     *
     * @param name
     */
    getComponent(name: string): LearnDrawerComponent
    {
        return this._componentRegistry.get(name);
    }
}
